.loginUp {
  display: grid;
  grid-template-columns: repeat(11, 1fr);
  grid-template-rows: repeat(12, 1fr);
  grid-column-gap: 0px;
  grid-row-gap: 0px;
}

.Login {
  padding: 5px;
  border-radius: 5px;
  grid-area: 2 / 4 / 11 / 6;
  box-shadow: 10px 10px 22px #cac5c5;
  background-image: linear-gradient(130deg, #fb8985, #b33253);
}

.signup {
  padding: 5px;
  border-radius: 5px;
  grid-area: 2 / 7 / 11 / 9;
  box-shadow: 10px 10px 22px #cac5c5;
  background-image: linear-gradient(40deg, #b33253, #fb8985);
}

input {
  width: -webkit-fill-available;
}

.input {
  margin-top: 5px;
  margin-bottom: 5px;
  max-width: 270px;
  border: 1px solid white;
}

.input:focus,
textarea :focus {
  border: 0.2vmin solid #fb8985;
}

textarea :focus {
  outline: 0 none;
}

.SubmiSignup {
  color: black;
  width: 100px;
  font-size: 20px;
  margin-top: 55px;
  border-radius: 5px;
  -webkit-text-stroke: thin;
  background-color: white;
}
