.btnmyheaderPink {
  top: 65px;
  margin: 5px;
  border: none;
  border: 0px;
  width: 192px;
  height: 35px;
  border-radius: 15px;
  font-size: initial;
  padding-top: 2px;
  padding-bottom: 2px;
  padding-right: 7px;
  padding-left: 7px;
  color: rgb(255, 255, 255);
  box-shadow: 7px 7px 22px #cac5c5;
  background-image: linear-gradient(to bottom right, #fba490, #b83258);
}

.btnmyheaderBlue {
  top: 65px;
  margin: 5px;
  border: none;
  border: 0px;
  width: 192px;
  height: 35px;
  padding-top: 2px;
  font-size: initial;
  border-radius: 15px;
  padding-top: 0.5em;
  padding-bottom: 2px;
  padding-right: 7px;
  padding-left: 7px;
  background-image: linear-gradient(
    180deg,
    rgba(70, 74, 159, 1) 0%,
    rgba(90, 161, 213, 1) 100%
  ) !important;
  color: rgb(255, 255, 255);
}

.btnmyheader:hover {
  border: 0;
  /* width: 200px;
