.ListbtnForm {
  display: flex;
  color: #c74c64;
  font-size: 1.2pc;
  font-weight: 600;
  justify-content: center;
  padding-inline-start: 0;
}

li {
  padding: 2pc;
}


::marker {
  content: none;
}

div.ManagerForms {
  display: flex;
  padding: 2em;
  margin-top: 1fr;
  justify-content: center;
}

.FormContent {
  width: 300px;
  /*width: 800px;*/
  border-radius: 5px;
  border: 0.2vmin solid #666666;
  box-shadow: 10px 10px 22px #b6b3b3;
}

input.input {
  border: 0.2vmin solid #666666;
}

textarea {
  direction: ltr;
  max-width: 290px;
  border-radius: 5px;
}
